import {
  Box,
  CircularProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';

export default function EmbeddedGroupTable({
  currentGroup = '',
  excludeList = [],
  selectCallback = () => null,
}) {
  const [groupList, setGroupList] = useState([]);
  const [loading, setIsLoading] = useState(false);
  const authAPI = useAuthAPI();

  const handleRowSelect = async (groupName = null) => {
    if (selectCallback && groupName) {
      setIsLoading(true);
      selectCallback(groupName);
    }
  };

  useEffect(async () => {
    setIsLoading(true);
    let data = await authAPI.getGroups();
    if (data) {
      excludeList.push(currentGroup);
      const dataFilter = data.filter((group) => !excludeList.includes(group));
      setGroupList(dataFilter);
    }
    setIsLoading(false);
  }, []);

  return (
    <>
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress />
        </Box>
      ) : (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 300 }} aria-label='Embedded Group Table'>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {groupList?.map((currentGroup, index) => {
                return (
                  <TableRow
                    key={index}
                    onClick={() => handleRowSelect(currentGroup)}
                    style={{ cursor: 'pointer' }}
                    hover
                  >
                    <TableCell>{currentGroup}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </>
  );
}
