import { TextField } from '@mui/material';
import React from 'react';

export default function PolicyNameInput({
  placeholder = '',
  valueProp = '',
  setState = () => null,
  errorMessage = '',
}) {
  return (
    <>
      <TextField
        variant='outlined'
        style={{ width: '100%' }}
        error={!!errorMessage}
        helperText={errorMessage}
        placeholder={placeholder}
        onChange={(event) => {
          const rawText = event.target.value;
          if (rawText === '') {
            setState(rawText);
            return;
          }
          let inputText = event.target.value ?? '';
          if (inputText.trim()) {
            const sanitizedInput = inputText.replaceAll(' ', '_');
            const lastSanitizedChar = sanitizedInput[sanitizedInput.length - 1];
            const lastValuePropChar = valueProp[valueProp.length - 1];
            if (
              lastValuePropChar !== lastSanitizedChar ||
              (lastValuePropChar === lastSanitizedChar &&
                lastValuePropChar !== '_')
            ) {
              setState(sanitizedInput);
            }
          }
        }}
        inputProps={{
          'aria-label': `${placeholder + ' '} policy name text input`,
          'data-testid': 'newPolicyNameInputId',
        }}
        value={valueProp}
      />
    </>
  );
}
