import {
  Button,
  DialogTitle,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useRouter } from 'next/router';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';
import EmbeddedGroupTable from './embeddedGroupTable';
import PolicyNameInput from './policyNameInput';

export default function AdminGroupTable({
  embeddedMode = false,
  selectCallback = () => null,
  roleNameProp = '',
  groupNameProp = '',
}) {
  const router = useRouter();
  const [open, setOpen] = useState(false);
  const [newGroupName, setNewGroupName] = useState('');
  const [groupInputError, setGroupInputError] = useState('');
  const [groupList, setGroupList] = useState([]);
  const authAPI = useAuthAPI();

  const handleClickOpen = () => {
    setNewGroupName('');
    setGroupInputError('');
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setNewGroupName('');
  };

  const handleRowSelect = async (roleName = null) => {
    if (selectCallback && roleName) {
      await selectCallback(roleName);
    }
    handleClose();
  };

  const handleRouteToGroup = (groupName = '') => {
    if (groupName) {
      router.push('/admin/group/' + groupName);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (newGroupName?.trim() && newGroupName.replaceAll('_', '').length > 2) {
      let data = await authAPI.addGroup(newGroupName);
      if (data) {
        let newList = await authAPI.getGroups();
        setGroupList(newList);
      }
      handleClose();
    } else {
      setGroupInputError(
        'Please enter a name. Must be 3 characters or longer.'
      );
    }
  };

  const refreshContent = async () => {
    let data = [];
    if (roleNameProp) {
      data = await authAPI.getGroupsForRole(roleNameProp);
    } else if (groupNameProp) {
      data = await authAPI.getChildGroups(groupNameProp);
    } else {
      data = await authAPI.getGroups();
    }

    if (data) {
      setGroupList(data);
    }
  };

  useEffect(async () => {
    if (!embeddedMode) {
      await refreshContent();
    }
  }, []);

  useEffect(async () => {
    if (embeddedMode && !open) {
      refreshContent();
    }
  }, [open]);

  return (
    <>
      <strong style={{ float: 'left' }}>Groups</strong>
      <Button
        style={{ float: 'right', marginBottom: '0.5em' }}
        onClick={handleClickOpen}
      >
        Add Group
      </Button>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 300 }} aria-label='Admin Group Table'>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {groupList?.map((row, index) => {
              return (
                <TableRow key={index} hover>
                  <TableCell>
                    <Button onClick={() => handleRouteToGroup(row)}>
                      {row}
                    </Button>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog
        fullWidth={true}
        maxWidth={'sm'}
        open={open}
        onClose={handleClose}
        aria-labelledby='dialog-title'
        aria-describedby='dialog-description'
      >
        <DialogTitle id='dialog-title'>
          {embeddedMode ? 'Select New Group' : 'New Group'}
        </DialogTitle>
        <DialogContent>
          {embeddedMode ? (
            <EmbeddedGroupTable
              currentGroup={groupNameProp}
              excludeList={groupList}
              selectCallback={handleRowSelect}
            />
          ) : (
            <PolicyNameInput
              placeholder='New Group...'
              valueProp={newGroupName}
              setState={setNewGroupName}
              errorMessage={groupInputError}
            />
          )}
        </DialogContent>
        <DialogActions>
          {!embeddedMode ? (
            <Button onClick={handleSubmit}>Submit</Button>
          ) : null}
          <Button onClick={handleClose}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
